import React from 'react'
import Element from '../components/elements/Elements'
import { IProfile } from '../stores/articlesStore/article.store.interface'

interface IProfileComponent {
    profile: IProfile
}

const ProfileComponent = (props: IProfileComponent) => {
    const { profile } = props
    
    return (
        <Element.wrapper size='xl'>
            <Element.frame>
                <Element.image src={profile.photo} />
            </Element.frame>
            <Element.title>{profile.name}</Element.title>
            {profile.roles.map(rol => (
                <Element.resume key={rol}>{rol}</Element.resume>
            ))}
            <Element.space primary />
            <Element.separator />
            <Element.space primary />
            {profile.details.map(detail => (
                <Element.item key={detail}>{detail}</Element.item>
            ))}
        </Element.wrapper>
    )
}

export default ProfileComponent