import React from 'react'
import Element from '../components/elements/Elements'
import { IExperience } from '../stores/articlesStore/article.store.interface'

interface IExperienceComponent {
    experiences: Array<IExperience>
}

const ExperienceComponent = (props: IExperienceComponent) => {

    const generateSkill = (experiencie: IExperience) => (
        <>
            <Element.title>{experiencie.title}</Element.title>
            <Element.resume>{experiencie.resume}</Element.resume>
            <Element.text>{experiencie.text}</Element.text>
        </>
    )

    const generateSkills = (
        experienceList: IExperienceComponent['experiences']
    ) => experienceList.map((experiencie, index) => (
        <div key={experiencie.title}>
            {generateSkill(experiencie)}
            {index + 1 !== experienceList.length && <Element.separator />}
        </div>
    ))

    return (
        <Element.wrapper size='xl'>
            {generateSkills(props.experiences)}
        </Element.wrapper>
    )
}

export default ExperienceComponent