import React from 'react'
import Element from '../components/elements/Elements'
import { IEducation } from '../stores/articlesStore/article.store.interface'

interface IEducationComponent {
    education: Array<IEducation>
}

const EducationComponent = (props: IEducationComponent) => {
    const educationList = props.education

    const generateTags = (
        tags: IEducation['tags']
    ) => tags.map(tag => (<Element.tag key={tag}>{tag}</Element.tag>))

    const generateEducationItem = (education: IEducation) => (
        <>
            <Element.resume>{education.institute}</Element.resume>
            <Element.title>{education.title}</Element.title>
            <Element.text>{education.date}</Element.text>
            <Element.tags>
                {generateTags(education.tags)}
            </Element.tags>
        </>
    )

    const generateEducationItems = (
        educationList: Array<IEducation>
    ) => educationList.map((education, index) => (
        <div key={education.title}>
            {generateEducationItem(education)}
            {index + 1 !== educationList.length && <Element.separator />}
        </div>
    ))

    return (
        <Element.wrapper size='xl'>
            {generateEducationItems(educationList)}
        </Element.wrapper>
    )
}

export default EducationComponent

