import { combineReducers } from "redux";
import { createStore, applyMiddleware } from "redux";
import logger from "redux-logger";
import { persistStore, persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";

import { userStore } from "./userStore/user.store.dispatcher";
import {articleStore } from "./articlesStore/article.store.dispatcher";
import {UserStateInterface} from './userStore/user.store.interface'
import {IArticleState} from './articlesStore/article.store.states'

const persistConfig = {
  key: "authType",
  storage: storage,
  whitelist: ["authType"] // which reducer want to store
};

export interface IStores {
  userStore: UserStateInterface,
  articleStore: IArticleState
}

const reducers = combineReducers({
  userStore,
  articleStore
});

const pReducer = persistReducer(persistConfig, reducers);
const middleware =  applyMiddleware(logger)

let store: any;
if(process.env.NODE_ENV ==='development') store = createStore(pReducer, middleware);
else store = createStore(pReducer)

const persistor = persistStore(store);
export { persistor };
export default store;
